<template>
  <main class="whole-container">
    <section class="whole-form-container dark-mode">
      <!--izquierda-->
      <section class="whole-left-container" >
        <section class="section-left-info flex ">
            <img :src="stage" class="stage">
          <div class="second-flex-part">
            <h2 v-html="course.title" class="title"></h2>
            <h4 v-html="course.subtitle" class="subtitle"></h4>
            <p v-html="course.price" class="price"></p>

            <ul class="details">
              <li class="flex">
                <img src="../assets/purple-tick.svg" alt="">
                Materials included
              </li>
              <li class="flex">
                <img src="../assets/purple-tick.svg" alt="">
                Final project
              </li>
              <li class="flex">
                <img src="../assets/purple-tick.svg" alt="">
                Our certificate
              </li>
            </ul>

            <section class="course-final-info flex">
              <article class="first">
                <p v-html="course.start.text"></p>
                <p v-html="course.start.value"></p>
              </article>
              <article class="second">
                <p v-html="course.duration.text"></p>
                <p v-html="course.duration.value"></p>
              </article>

            </section>
          </div>
         </section> 
      </section>
      <!--derehcha-->
      <section class="whole-right-container">
        <VueSlickCarousel v-bind="settings" ref="carousel" class="carousel">
            <div class="section-right-inputs">
              <form action="">
                <h2 v-html="form.title1"></h2>
                <div class="input-container relative" :id="input.containerId" v-for="(input, index) in firstInputs" :key="index">
                  <input :type="input.type" :id="input.id" @keyup="handleFpart(input.id, input.containerId, input.regex)" required autocomplete="off">
                  <label :for="input.id" v-html="input.label"></label>
                  <p class="warn" v-html="input.warn"></p>
                  <img class="tick" src="../assets/green-tick.svg">
                  <img class="cross" src="../assets/red-cross.svg">
                </div>    

                <div class="button-container">
                  <button type="button" disabled v-html="form.button1" class="main-button" @click="secondStage"></button>
                </div>

              </form>
            </div>
            <div class="section-right-inputs second-form">
              <img :src="arrow" class="arrow" @click="prevStage"/>          
              <form action="">
                <h2 v-html="form.title2"></h2>
                <div class="input-container relative" :id="input.containerId" v-for="(input, index) in secondInputs" :key="index">
                  <input :type="input.type" :id="input.id" @keyup="handleSpart(input.id, input.containerId, input.regex)" required autocomplete="off" :maxlength="input.max">
                  <label :for="input.id" v-html="input.label"></label>
                  <p class="warn" v-html="input.warn"></p>
                  <img class="tick" src="../assets/green-tick.svg">
                  <img class="cross" src="../assets/red-cross.svg">
                </div>                

              <div class="button-container flex">
                <button type="button" v-html="form.button2" class="secondary-button" ></button>
                <button type="button" disabled v-html="form.button3" class="main-button" @click="checkInfo"></button>
              </div>

              </form>
            </div>
        </VueSlickCarousel>
      </section>

    </section>
    <section class="modal-container">
      <article class="modal">
        <h3 v-html="modal.title"></h3>
        <div class="loader" @animationend="finishCicle"></div>
        <h3 v-html="modal.subtitle"></h3>
        <h4 v-html="modal.lastMessage"></h4>
        <button class="main-button" v-html="modal.button" @click="reload"></button>
      </article>
    </section>
  </main>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  name: 'HelloWorld',  
  components:{VueSlickCarousel},
  data(){
    return{
      course:{
        title:'Illustration Toolbox',
        subtitle:'A method to illustrate',
        price:'€29.99',
        start:{
          text:'Starts',
          value:'14/10' 
        },
        duration:{
          text:'Duration',
          value:'3 classes'
        }
      },
      form:{
        title1:'1. Provide your data',
        button1:'GO TO PAYMENT',
        title2:'2. Provide your card data',
        button2:'PAY WITH PAYPAL',
        button3:'FINISH PAYMENT'
      },
      stage:require('../assets/stage1.svg'),
      arrow:require('../assets/arrow.svg'),
      firstInputs:[
        {
          containerId:'firstInput',
          type:'text',
          id:'first-part-name',
          label:'NAME',
          warn:'Give a valid name, please',
          regex:new RegExp(/(\w.+\s).+/) 
        },
        {
          containerId:'secondInput',
          type:'email',
          id:'first-part-email',
          label:'E-MAIL',
          warn:'Give a valid e-mail, please',
          regex:new RegExp('.{1,}@[^.]{1,}')
        }
      ],
      secondInputs:[
        {
          containerId:'thirdInput',
          type:'text',
          id:'second-part-name',
          label:'CARD NAME',
          warn:'Introduce a valid name, please',
          regex:new RegExp(/(\w.+\s).+/),
          max:80
        },
        {
          containerId:'fourthInput',
          type:'text',
          id:'second-part-card-number',
          label:'CARD NUMBER',
          warn:'Introduce only numbers, please',
          regex:new RegExp('[0-9]{16,}$'),
          max:16
        },
        {
          containerId:'fifthInput',
          type:'text',
          id:'second-part-exp-date',
          label:'EXP. DATE',
          warn:'Introduce a valid exp. date, please',
          regex:new RegExp('([0-1][0-9][/][0-2][0-9])'),
          max:5
        },
        {
          containerId:'sixthInput',
          type:'text',
          id:'second-part-cvv',
          label:'CVV',
          warn:'Introduce only numbers, please',
          regex:new RegExp('^[0-9]{3,}$'),
          max:3
        }
      ],
      settings:{
        dots: false,
        arrows:false,
        adaptiveHeight:true,
        infinite:false,
        swipe:false,
        
      },
      modal:{
        title:'Processing your card info',
        subtitle:'Wait a minute, please',
        lastMessage:'',
        button:'BACK TO HOME'
      },
      congratUser:{
        userName:'',
        mailDir:'your email'
      }
    }
  },
  methods:{
    prevStage(){
      this.$refs.carousel.prev()

      this.stage = require("../assets/stage1.svg")
    },
    secondStage(){      
      this.$refs.carousel.next()

      let fInput = document.querySelector(`#first-part-name`)
      let sInput = document.querySelector(`#first-part-email`)

      this.congratUser.userName = fInput.value
      this.congratUser.mailDir = sInput.value

      this.stage = require("../assets/stage2.svg")
    },
    handleFpart(inputId, labelCont, inputRegex){
      let fInput = document.querySelector(`#first-part-name`)
      let sInput = document.querySelector(`#first-part-email`)

      let input = document.querySelector(`#${inputId}`)
      let label = document.querySelector(`#${labelCont} label`)
      let warn = document.querySelector(`#${labelCont} .warn`)
      let tick = document.querySelector(`#${labelCont} .tick`)
      let cross = document.querySelector(`#${labelCont} .cross`)

      let Button = document.querySelector(`.main-button`)
      //console.log(Regex)

      input.value.length >= 1 ? label.style.transform = 'translate(0rem, -.8rem) scale(.8)' : label.style.transform = 'translate(.5rem, .6rem) scale(1)'

      if(input.value.match(inputRegex)){
        tick.style.display = 'block'
        cross.style.display = 'none'
        warn.style.display = 'none'
        

      } else{
        tick.style.display = 'none'
        cross.style.display = 'block'
        warn.style.display = 'block'
      }

      if(fInput.value.match(this.firstInputs[0].regex) && sInput.value.match(this.firstInputs[1].regex)){
        Button.disabled = false
        Button.style.opacity = '1'
        Button.style.cursor = 'pointer'
      } else{
        Button.disabled = true
        Button.style.opacity = '.5'
        Button.style.cursor = 'not-allowed'
      }
    },
    handleSpart(inputId, labelCont, inputRegex){
      let thInput = document.querySelector(`#second-part-name`)
      let foInput = document.querySelector(`#second-part-card-number`)
      let fiInput = document.querySelector(`#second-part-exp-date`)
      let sxInput = document.querySelector(`#second-part-cvv`)

      let input = document.querySelector(`#${inputId}`)
      let label = document.querySelector(`#${labelCont} label`)
      let warn = document.querySelector(`#${labelCont} .warn`)
      let tick = document.querySelector(`#${labelCont} .tick`)
      let cross = document.querySelector(`#${labelCont} .cross`)

      let Button = document.querySelector(`.second-form .main-button`)
      let allInputs = document.querySelectorAll(`.second-form form input`)

      //console.log(Regex)

      input.value.length >= 1 ? label.style.transform = 'translate(0rem, -.8rem) scale(.8)' : label.style.transform = 'translate(.5rem, .6rem) scale(1)'

      if(input.value.match(inputRegex)){
        tick.style.display = 'block'
        cross.style.display = 'none'
        warn.style.display = 'none'
        

      } else{
        tick.style.display = 'none'
        cross.style.display = 'block'
        warn.style.display = 'block'
      }

      if(thInput.value.match(this.secondInputs[0].regex) && 
        foInput.value.match(this.secondInputs[1].regex) && 
        fiInput.value.match(this.secondInputs[2].regex) && 
        sxInput.value.match(this.secondInputs[3].regex))
        {
        Button.disabled = false
        Button.style.opacity = '1'
        Button.style.cursor = 'pointer'
      } else {
        Button.disabled = true
        Button.style.opacity = '.5'
        Button.style.cursor = 'not-allowed'
      }
    },
    checkInfo(){
      let modal = document.querySelector('.modal-container')

      modal.style.display = 'block'
    },
    finishCicle(){
      this.modal.title = `Congratulations ${this.congratUser.userName}!`
      this.modal.subtitle = 'You have just acquired our <span class="purple">Illustration Toolbox</span> course.'
      this.modal.lastMessage = `Soon we will send your receipt to <span class="purple">${this.congratUser.mailDir}</span>`

      document.querySelector('.modal-container .modal .loader').style.display = 'none';
      document.querySelector('.modal-container .modal .main-button').style.display = 'block';
    },
    reload(){
      location.reload()
    },
  },
}
</script>

